.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5); /* 반투명 배경 */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* 다른 요소 위에 표시 */
}

.modal-content {
  background: white; /* 모달 배경 색상 */
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* 그림자 효과 */
  width: 300px; /* 너비 조정 */
  text-align: center; /* 텍스트 가운데 정렬 */
}

.modal-buttons {
  margin-top: 20px; /* 버튼 간 간격 추가 */
}

.modal-buttons button {
  margin: 0 5px; /* 버튼 간 간격 */
}
